.cost-date{
    display: flex;
    flex-direction: column;
    width: 5.5rem;
    height: 5.5rem;
    border: 1px solid rgb(222, 237, 248);
    background-color: rgb(16, 39, 243);
    color: white;
    border-radius: 12px;
    align-items: center;
    justify-content: center
}

.cost-date__month {
    font-size: 0.75rem;
    font-weight: bold;
}

.cost-date__year {
    font-size: 0.75rem;
}

.cost-date__day{
    font-size: 0.75rem;
    font-weight: bold;
}